import './App.css';
import { useState, useEffect } from 'react';
import Square from "./components/Square";
import { WinningCond } from './components/WinningCond';

function App() {

  const [grille, setGrille] = useState(["", "", "", "", "", "", "", "", ""])
  const [joueur, setJoueur] = useState("O");
  const [resultat, setResultat] = useState({winner: "none", state: "none"})
  

  useEffect(() => {

    verifVictoire();
    verifEgalite();
    

    if(joueur === "X") {

      setJoueur("O");

    } 
    
    else {

      setJoueur("X");

    }

  }, [grille]);

  useEffect(() => {
    if (resultat.state !== "none") {

      alert(`Partie terminÃ©e ! Le vainqueur est le joueur : ${resultat.winner}`)
      
    }
    

  }, [resultat]);

  const chooseSquare = (square) => {

    setGrille(grille.map((val, index) => {

        if(index === square && val === "") {

            return joueur;

        }

        return val;

    }));
  };

  const verifVictoire = () => {

    WinningCond.forEach((currCond) => {

      const firstjoueur = grille[currCond[0]];

      if(firstjoueur === "") return;

      let foundWinningPattern = true;

      currCond.forEach((index) => {

        if(grille[index] !== firstjoueur) {

          foundWinningPattern = false;

        }

      })

      if(foundWinningPattern) {

          setResultat({winner: joueur, state: "won"});

      }

    });

  };

  const verifEgalite = () => {

    let filled = true;
    grille.forEach((square) => {

      if(square === "") {

        filled = false

      }

    })

    if(filled) {

      setResultat({winner: "Aucun vainqueur", state: "Tie"});

    }

  }

  const relancerJeu = () => {

    setGrille(["", "", "", "", "", "", "", "", ""]);
    setJoueur("O");
   }

  return (
    <div className="App">

      <h2>Tic..Tac..Toe</h2>

      <div className="grille">

        <div className="colonne">
          <Square val={grille[0]} chooseSquare={() => {
            chooseSquare(0)
          }}/>

            <Square val={grille[1]} chooseSquare={() => {
            chooseSquare(1)
          }}/>

              <Square val={grille[2]} chooseSquare={() => {
            chooseSquare(2)
          }}/>
        </div>
        <div className="colonne">
          <Square val={grille[3]} chooseSquare={() => {
            chooseSquare(3)
          }}/>

            <Square val={grille[4]} chooseSquare={() => {
            chooseSquare(4)
          }}/>

              <Square val={grille[5]} chooseSquare={() => {
            chooseSquare(5)
          }}/>
        </div>
        <div className="colonne">
          <Square val={grille[6]} chooseSquare={() => {
            chooseSquare(6)
          }}/>

            <Square val={grille[7]} chooseSquare={() => {
            chooseSquare(7)
          }}/>

              <Square val={grille[8]} chooseSquare={() => {
            chooseSquare(8)
          }}/>

        </div>

      </div>

      <button onClick={relancerJeu}>Relancer une partie !</button>
     
    </div>
  );
}

export default App;
